* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f5f5f5;
  color: #333;
}

/* Futurio Theme Styles */
.futurio-home-root {
  min-height: 100vh;
  width: 100vw;
  background: #f5f8fd;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
}

.futurio-navbar {
  width: 100vw;
  background: #fff;
  box-shadow: 0 2px 12px rgba(0,0,0,0.07);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 48px;
  height: 72px;
  position: sticky;
  top: 0;
  z-index: 10;
}
.futurio-navbar-logo {
  font-size: 2.1rem;
  font-weight: bold;
  letter-spacing: 2px;
  color: #222;
  cursor: pointer;
  font-family: 'Montserrat', 'Segoe UI', Arial, sans-serif;
}
.futurio-navbar-menu {
  display: flex;
  gap: 32px;
  list-style: none;
  margin: 0;
  padding: 0;
}
.futurio-navbar-menu li {
  font-size: 1.1rem;
  color: #444;
  cursor: pointer;
  transition: color 0.2s;
  padding-bottom: 2px;
  border-bottom: 2px solid transparent;
}
.futurio-navbar-menu li.active, .futurio-navbar-menu li:hover {
  color: #2a509c;
  border-bottom: 2px solid #2a509c;
}
.futurio-navbar-btn {
  background: #2a509c;
  color: #fff;
  font-weight: 600;
  border: none;
  border-radius: 6px;
  padding: 12px 32px;
  font-size: 1.1rem;
  cursor: pointer;
  box-shadow: 0 2px 8px #2a509c22;
  transition: background 0.2s;
}
.futurio-navbar-btn:hover {
  background: #1e386b;
}

.futurio-hero {
  width: 100vw;
  min-height: 540px;
  height: 80vh;
  background-size: cover;
  background-position: center;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.futurio-hero-animated-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100%;
  z-index: 1;
  pointer-events: none;
  overflow: hidden;
}
.animated-particles {
  width: 100vw;
  height: 100%;
  min-height: 320px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  display: block;
  pointer-events: none;
  background: transparent;
}

.futurio-hero-overlay {
  position: absolute;
  top: 0; left: 0;
  width: 100vw;
  height: 100%;
  z-index: 2;
  background: linear-gradient(180deg, rgba(42,80,156,0.60) 0%, rgba(42,80,156,0.10) 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.futurio-hero-content {
  z-index: 3;
  position: relative;
  text-align: center;
  color: #fff;
  margin-top: 40px;
}
.futurio-hero-subtitle {
  font-size: 1.1rem;
  letter-spacing: 3px;
  color: #e0e7ff;
  margin-bottom: 18px;
  font-weight: 500;
}
.futurio-hero-title {
  font-size: 3.2rem;
  font-weight: 800;
  letter-spacing: 2px;
  margin-bottom: 18px;
  color: #fff;
  text-shadow: 0 4px 24px #2a509c55;
}
.futurio-hero-desc {
  font-size: 1.2rem;
  color: #e0e7ff;
  margin-bottom: 32px;
  max-width: 540px;
  margin-left: auto;
  margin-right: auto;
}
.futurio-hero-buttons {
  display: flex;
  gap: 18px;
  justify-content: center;
}
.futurio-btn-primary {
  background: #2a509c;
  color: #fff;
  font-weight: 700;
  border: none;
  border-radius: 8px;
  padding: 16px 38px;
  font-size: 1.1rem;
  cursor: pointer;
  box-shadow: 0 2px 12px #2a509c33;
  transition: background 0.2s;
}
.futurio-btn-primary:hover {
  background: #1e386b;
}
.futurio-btn-secondary {
  background: #fff;
  color: #2a509c;
  font-weight: 700;
  border: 2px solid #2a509c;
  border-radius: 8px;
  padding: 16px 38px;
  font-size: 1.1rem;
  cursor: pointer;
  box-shadow: 0 2px 12px #2a509c22;
  transition: background 0.2s, color 0.2s;
}
.futurio-btn-secondary:hover {
  background: #2a509c;
  color: #fff;
}

.scroll-down-indicator {
  position: absolute;
  left: 50%;
  bottom: 32px;
  transform: translateX(-50%);
  z-index: 3;
}
.scroll-down-indicator span {
  display: block;
  width: 32px;
  height: 32px;
  border-bottom: 4px solid #fff;
  border-right: 4px solid #fff;
  transform: rotate(45deg);
  margin: 0 auto;
  animation: scrollDown 1.2s infinite;
  opacity: 0.8;
}
@keyframes scrollDown {
  0% { transform: translateY(0) rotate(45deg); opacity: 0.8; }
  50% { transform: translateY(12px) rotate(45deg); opacity: 1; }
  100% { transform: translateY(0) rotate(45deg); opacity: 0.8; }
}

.futurio-main-content {
  width: 100vw;
  background: #f5f8fd;
  padding: 64px 0 0 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.futurio-section {
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 2px 16px #0001;
  padding: 48px 32px;
  margin-bottom: 40px;
  max-width: 900px;
  width: 90vw;
  text-align: center;
}
.futurio-footer {
  width: 100vw;
  background: #fff;
  color: #888;
  text-align: center;
  padding: 24px 0 16px 0;
  font-size: 1rem;
  margin-top: 32px;
  box-shadow: 0 -2px 12px #0001;
}

.futurio-section.features-section {
  background: linear-gradient(120deg, #e0e7ff 0%, #f5f8fd 100%);
  box-shadow: 0 4px 24px #7b2ff211;
  border: 1.5px solid #e0e7ff;
  position: relative;
}
.features-list {
  list-style: none;
  padding: 0;
  margin: 32px auto 0 auto;
  max-width: 600px;
  text-align: left;
  background: rgba(255,255,255,0.85);
  border-radius: 14px;
  box-shadow: 0 2px 12px #2a509c11;
  padding: 24px 18px;
}
.features-list li {
  position: relative;
  padding-left: 32px;
  margin-bottom: 18px;
  font-size: 1.13rem;
  color: #2a509c;
  line-height: 1.6;
  background: none;
}
.features-list li:before {
  content: '\2714'; /* checkmark */
  position: absolute;
  left: 0;
  top: 2px;
  color: #7b2ff2;
  font-size: 1.2em;
  font-weight: bold;
}

@media (max-width: 900px) {
  .futurio-navbar {
    padding: 0 16px;
  }
  .futurio-navbar-logo {
    font-size: 1.4rem;
  }
  .futurio-navbar-menu {
    gap: 16px;
  }
  .futurio-hero-title {
    font-size: 2.1rem;
  }
  .futurio-hero-desc {
    font-size: 1rem;
  }
  .futurio-section {
    padding: 32px 8px;
  }
}
